#phone {
	position: absolute;
    width: 230px;
    height: 462px;
	right: 50vw;
	bottom: 2vh;
}

#phone #phone-model {
	position: absolute;
    background-image: url(../img/phone/model.png);
    width: 230px;
    height: 462px;
    background-size: 100%;
}

#phone #phone-bg {
	position: absolute;

    width: 205px;
    margin: 11px 12.5px;
    height: 440px;
	background-size: 205px 440px;
}

#phone #phone-bg.green
{
	background-color: #5CB859;
}

#phone #phone-bg.orange
{
	background-color: #ff5722;
}

#phone #phone-bg.main {
	background-image: url(../img/phone/bg.png);
}

#phone #phone-bg .phone-signal
{
	position: absolute;
	font-size: 10px;
	color: white;
    right: 20px;
    top: 3px;
}

#phone #phone-bg .phone-time
{
	position: absolute;
	top: 2px;
	color: white;
    left: 15px;
    font-size: 12px;
}

#phone #phone-body {
	position: absolute;
    margin: 30px 12.5px 11px;
    width: 205px;
    height: 421px;
    border-radius: 0px 0px 20px 20px;
}

#phone #phone-home {
	width: 100px;
    height: 7px;
    position: absolute;
    left: 65px;
    bottom: 20px;
    background-color: rgba(0,0,0,0.1);
    border-radius: 30px;
}

#phone #phone-home:hover
{
	background-color: rgba(0,0,0,0.4);
}

#phone .app-home-body .phone-app {
	float: left;
	text-align: center;
}
#phone .app-home-body .phone-app img
{
	height: 36px;
	width: 36px;
	margin: 7px;
}

#phone .app-home-body .phone-app .focus {
	background: rgba(255,255,255,0.5);
	position: absolute;
	border-radius: 9px;
	height: 36px;
	width: 36px;
	margin: 7px;
}

#phone .app-home-body .phone-app p
{
	color:#fff;
	margin: 0px;
	font-family: Verdana, Helvetica, Arial, sans-serif;
	font-size:8px;
}   

#phone .app-message,
#phone .app-phone {
	background-color: #fff;
    height: 100%;
    border-radius: 20px;
}

#phone .app-phone .app-phone-header,
#phone .app-message .app-message-header
{
	color: #fff;
	background-color: #5CB859;
	height: 23px;
	text-align: center;
}

#phone .app-phone .app-phone-dial
{
	height: 337px;
	background-color: #fff;
	color:#000;
	padding:10px;
}
#phone .app-phone .app-phone-dial .dial-number
{
    text-align: center;
    color: #868686;
    height: 30px;
    font-size: 24px;
    margin: 14px 0px;
}

#phone .app-phone .app-phone-dial .glyphicons-delete {
	position: absolute;
	right: 0px;
	margin-right: 40px;
}

#phone .app-phone .app-phone-dial .glyphicons-delete:hover {
	color: black;
}

#phone .app-phone .app-phone-dial .clean,
#phone .app-phone .app-phone-dial .number
{
    text-align: center;
    float: left;
    padding: 14px;
    width: 20px;
    height: 20px;
    margin: 5px;
    border-radius: 30px;
    border: 1px solid #ccc;
}
#phone .app-phone .app-phone-dial .call
{
    text-align: center;
	float: left;
    padding: 14px;
    width: 20px;
    height: 20px;
    margin: 5px;
	color:#fff;
    border-radius: 30px;
    background-color: #5cb85c;
    border-color: #4cae4c;
}
#phone .app-phone .app-phone-dial .call:hover
{
    background-color: #47a447;
    border-color: #398439;
}
#phone .app-phone .app-phone-dial .number:hover,
#phone .app-phone .app-phone-dial .clean:hover
{
	background-color:#ccc;
	text-align: center;
}
#phone .app-phone .app-phone-recent
{
	background-color:#fff;
	padding: 2px;
	height:352px;
}

#phone  .app-phone .app-phone-recent p
{
	color: #868686;
	font-size: 10px;
    margin: 0px;
}

#phone  .app-phone .app-phone-recent li {
    padding: 10px;
    border-bottom: 1px solid #ccc;
    -webkit-transition: background-color .5s;
    -o-transition: background-color .5s;
    transition: background-color .5s;
}
#phone  .app-phone .app-phone-recent li:hover {
    padding: 10px;
    background: rgba(0,0,0,0.2);
    -webkit-transition: background-color .5s;
    -o-transition: background-color .5s;
    transition: background-color .5s;
}
#phone  .app-phone .app-phone-botton
{
	border-top: 1px solid #ccc;
	border-radius: 20px;
	color:#868686;
	background-color: #fff;
	height:40px;
}
#phone  .app-phone .app-phone-botton .recent
{
	padding:10px 0px;
	height: 20px;
	float:left;
	width:101px;
	text-align: center;
	border-right: 1px solid #ccc;
}

#phone .app-phone .app-phone-botton .recent:hover {
	border-radius: 20px 0px 0px 20px;
	background-color: #ccc;
	color:#000;	
}    

#phone .app-phone .app-phone-botton .dial:hover {
	border-radius: 0px 20px 20px 0px;
	background-color: #ccc;
	color:#000;	
}


#phone .app-phone .app-phone-botton .dial
{
	padding:10px 0px;
	height: 20px;
	float:left;
	text-align: center;   
	width:102px;	
}

#phone .app-message .app-message-header .glyphicons-edit:hover,
#phone .app-message .app-message-header .glyphicons-chevron-left:hover
{
	background-color: rgba(255,255,255,0.3);
    border-radius: 5px;
}
#phone .app-message .app-message-header .glyphicons-edit,
#phone .app-message .app-message-header .glyphicons-chevron-left
{
    position: absolute;
	padding:2px;
    left: 10px;
}

#phone .app-message .app-message-list
{
	height: 394px;
	padding: 2px;
	color:#000;
	overflow-y: scroll !important;
}
#phone .app-message .app-message-list ul {
	margin: 0px;
    padding: 0px;

}
#phone .app-message .app-message-list li {
	padding: 10px;
	list-style-type: none;
    border-bottom: 1px solid #ccc;
}

#phone .app-message .app-message-list li p
{
	color: #868686;
	font-size: 10px;
    margin: 0px;
}

#phone .app-message .app-message-list li:hover {
    padding: 10px;
    background: rgba(0,0,0,0.2);
}

#phone .app-message .app-message-history-body {
	height: 364px;
}

#phone .app-message .app-message-history-body .left
{
	width: 120px;
	background-color: #ccc;
	border-radius: 10px;
	font-size: 12px;
    margin: 5px;
    padding: 5px;	
}
#phone .app-message .app-message-history-body .right
{
	color:#fff;
	width: 120px;
	background-color: #428bca;
	border-radius: 10px;
	font-size: 12px;
    margin: 5px;
	margin-left: auto;
    padding: 5px;
}

#phone .app-message .app-message-history-botton input
{
	border-radius: 20px 0px 0px 20px;
	width: 139px;
}
#phone .app-message .app-message-history-botton button
{
	border-radius: 0px 20px 20px 0px;
	height: 34px;
	padding: 4px 10px;
	width: 40px;
	float: right;
}


#phone .app-message .app-message-new button
{
    border-radius: 0px 0px 20px 20px;
    width: 205px;
}
#phone .app-message .app-message-new input
{
	width: 178px;
}
#phone .app-message .app-message-new textarea
{
	height: 318px;
    width: 179px;
}

#phone .app-911 {
	background-image: -webkit-gradient(linear , left top, left bottom , from(#f44336) , to(#b71c1c));
	background-image: -webkit-linear-gradient(#f44336 , #b71c1c);
	background-image: -o-linear-gradient(#f44336 , #b71c1c);
	background-image: linear-gradient(#f44336 , #b71c1c);
	height: 100%;
	border-radius: 0px 0px 20px 20px;
	color: #fff;
	text-align: center;
}

#phone .app-911 .header {
    font-size: 18px;
    padding: 10px 10px;
}

#phone h3 {
	color: white;
	margin: 30px 0px;
}

#phone .app-911 ul {
	list-style-type: none;
	margin:0px;
	padding: 0px;
}

#phone .app-911 li {
	padding: 8px;
	color: #000;
	background-color: white;
	margin: 5px;
}

#phone .app-911 li:hover {
	background-image: -webkit-gradient(linear   , left top, left bottom   , from(#ef5350)   , to(#f44336));
	background-image: -webkit-linear-gradient(#ef5350   , #f44336);
	background-image: -o-linear-gradient(#ef5350   , #f44336);
	background-image: linear-gradient(#ef5350   , #f44336); 
	color: white;
	outline: 2px solid white;
}

#phone .app-911 input {
	border-color: #b71c1c ;
	background-color:rgba(255,255,255,0.8);
	outline: 0;
}

#phone .app-911 input:focus {
	background-color: white;
}

#phone .app-911 .glyphicons-unshare,
#phone .app-911 .glyphicons-edit
 {
	padding: 8px;
	color: #000;
    border: 1px solid #b71c1c;
    width: 70px;
    margin: 5px;
    background-color: rgba(255,255,255,0.8);
}

#phone .app-911 .glyphicons-unshare:hover,
#phone .app-911 .glyphicons-edit:hover {
	background-color: white;
}

#phone .app-info .app-info-header {
	color: #fff;
	background-color: #ff5722;
	height: 25px;
	text-align: center;
}

#phone .app-info .app-info-body {
	height: 396px;
    border-radius: 0px 0px 20px 20px;
	background: #fff;
}

#phone .app-info .app-info-body ul {
	margin: 0px;
    padding: 0px;

}
#phone .app-info .app-info-body li {
	padding: 10px;
	list-style-type: none;
    border-bottom: 1px solid #ccc;
}

#phone .app-info .app-info-body li p
{
	color: #868686;
	font-size: 10px;
    margin: 0px;
}

#phone .app-info .app-info-body li:hover {
    padding: 10px;
    background: rgba(0,0,0,0.2);
}

#phone .phone-alert {
	position: absolute;
	background: rgba(255,255,255,0.7);
	border: 1px solid #ccc;
	border-radius: 5px;
	color: rgb(27, 27, 27);
	width: 173px;
	z-index: 1;
	margin: 0px 5px; 
	padding: 10px;
}

#phone .animation-appear {
    opacity: 0;
    -webkit-transform: translate3d(0, -50, 0);
            transform: translate3d(0, -50, 0);
}
#phone .animation-appear.animation-appear-active {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}
#phone .animation-enter {
  opacity: 0;
  -webkit-transform: translateY(-50px);
      -ms-transform: translateY(-50px);
          transform: translateY(-50px);
}
#phone .animation-enter.animation-enter-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
 
#phone .animation-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 50, 0);
          transform: translate3d(0, 50, 0);
}
 
#phone .animation-leave.animation-leave-active {
  opacity: 0;
  -webkit-transform: translateY(50px);
      -ms-transform: translateY(50px);
          transform: translateY(50px);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

