#alert {
	position: absolute;
	width: 360px;
	left: 0vw;
  bottom: 20vh;
	z-index: 1000;	    
}
#alert ul li {
    margin: 4px 0;
    overflow: hidden;
    position: relative;
    -webkit-box-shadow: rgba(0, 0, 0, 0.298039) 0 0 5px 0;
            box-shadow: rgba(0, 0, 0, 0.298039) 0 0 5px 0;
    padding: 1.25em;
    font-size: 14px;
}
#alert .default {
  background-color: #fff;
  color: #1d1d1d; }

#alert .warning {
  background-color: #FA6800;
  color: #fff; }

#alert .error {
  background-color: #CE352C;
  color: #FFF; }

#alert .information {
  background-color: #1BA1E2;
  color: #FFF; }

#alert .success {
  background-color: #60A917;
  color: #fff; }

#alert .animation-appear {
    opacity: 0;
    -webkit-transform: translate3d(-250, 0, 0);
            transform: translate3d(-250, 0, 0);
}
#alert .animation-appear.animation-appear-active {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}
#alert .animation-enter {
  opacity: 0;
  -webkit-transform: translateX(-250px);
      -ms-transform: translateX(-250px);
          transform: translateX(-250px);
}
#alert .animation-enter.animation-enter-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
 
#alert .animation-leave {
  opacity: 1;
  -webkit-transform: translate3d(250, 0, 0);
          transform: translate3d(250, 0, 0);
}
 
#alert .animation-leave.animation-leave-active {
  opacity: 0;
  -webkit-transform: translateX(250px);
      -ms-transform: translateX(250px);
          transform: translateX(250px);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}


