#interaction .menu {
    position: absolute;
    width: 300px;
    height: 100%;
    top: 0px;
    right: 0px;
    color: #fff;
    padding: 10px;
    background: -webkit-gradient(linear, right top, left top, from(rgba(0,0,0,1)), to(rgba(0,0,0,0.2)));
    background: -webkit-linear-gradient(right, rgba(0,0,0,1), rgba(0,0,0,0.2));
    background: -o-linear-gradient(right, rgba(0,0,0,1), rgba(0,0,0,0.2));
    background: linear-gradient(to left, rgba(0,0,0,1), rgba(0,0,0,0.2));
    z-index: 2;
    -webkit-animation-name: slideLeft;
            animation-name: slideLeft;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s; 
    -webkit-animation-timing-function: ease-in-out; 
            animation-timing-function: ease-in-out;    

}

.cursor {
    width: 8px;
    height: 8px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    position: absolute;
    z-index: 15;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); 
  }
  
  .cursor.focus {
    background: rgba(255, 0, 0, 0.5)!important;
  }


#interaction .header {
    font-size: 24px;
    line-height: 54px;
    font-weight: bolder
}

#interaction ul {
    width: 300px;
	color: #fff;
	height: 100%;
	padding: 0px;
	margin: 0px;
}

#interaction li {
    padding: 14px;
	list-style-type: none;
	background: rgba(0,0,0,0.3);
	margin-top: 5px;
}
#interaction li.active
{
	background: rgba(0, 0, 0, 0.8) !important;
}

@-webkit-keyframes slideLeft {
    0% {
        -webkit-transform: translateX(350px);
                transform: translateX(350px);
    }   
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}

@keyframes slideLeft {
    0% {
        -webkit-transform: translateX(350px);
                transform: translateX(350px);
    }   
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}


