#control 
{
	width: 702px;
	height: 70%;
	position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
	z-index: 6;
}

#control .control-menu
{
	
	background: #009688;
	width: 647px;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	height: 40px;
	float: left;
	font-size: 14px;
	line-height: 40px;
	font-weight: bold;
	margin: 0px;
	padding: 0px;

}

#control .control-menu li {
	-webkit-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;
	color: #e0f2f1;
	list-style-type: none;
	display: inline;
	padding: 10px 10px 12px 10px
}

#control .control-menu li.focus {
	border-top: 2px solid white;
	color: #fff;
	background:rgba(255,255,255,0.1);
}

#control .control-close
{
	height: 40px;
	color: #fff;
	width: 40px;
	float: right;
	margin-right: 10px;
	text-align: center;
	line-height: 37px;
	font-size: 16px;
	background: #009688;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}

#control .default-text {
	float: left;
	padding: 24px;
	font-size: 24px;
	color: #fff;
	background-color: rgba(0, 0, 0, 0.5);

}
#control .control-close:hover {
	background: #00796b;
}

#control .players {
	color: #fff;
}
#control .players tbody {
	max-height: 60vh;
}
#control .players tr {
	background-color: rgba(0, 0, 0, 0.5);
}

#control .players .playerId {
	width: 75px;
	text-align: center;
	margin-right: 3px;
}

#control .players .playerName {
	width: 513px;
	text-align: left;
	padding: 8px;
	margin-right: 3px;
}


#control .players .playerPing {
	width: 75px;
}

#control .item-menu {
    float: left;
    list-style-type: none;
    margin: 0px;
    padding: 0px;
    color: #fff;
    text-align: center;
    width: 150px;
}

#control .item-menu li.focus {
    background-color:  #009688;
}

#control .item-menu li{
    padding: 8px;
    background-color: rgba(0, 0, 0, 0.5);
    margin: 5px 5px 5px 0px;
}

#control .item-body {
	background-color: rgba(0, 0, 0, 0.5);
	margin-top: 5px;
	color: #fff;
	padding: 8px;
	width: 526px;
	max-height: 60vh;
	float: left;
}

#control .item-body button{
	margin-top: -3px;
	height: 34px;
}

#control .item-body input {
	width: 250px;
}

#control .profile .user {
	background: rgba(0,0,0,0.5);
    width: 250px;
    color: #fff;
    margin-top: 5px;
    padding: 15px;
    float: left;
    font-size: 18px;
    line-height: 24px;
    font-weight: bold;
    list-style-type: none;
}

#control .profile .items {
	list-style-type: none;
}

#control .profile .items li {
	background-color: rgba(0, 0, 0, 0.5);
	padding: 8px;
	margin-left: 5px;
	margin-top: 5px;
	float: left;
}

#control .profile .edit-items{
	width: 400px;
	float: left;
	height: 100px;
	margin: 5px 0px 0px 5px;
	background: rgba(0,0,0,0.5);
}



