.scroll-block { overflow-y: scroll !important; }
.scroll-block::-webkit-scrollbar {width: 10px;}
.scroll-block.black::-webkit-scrollbar-thumb {background: rgba(0,0,0,0.1)}
.scroll-block.black:hover::-webkit-scrollbar-thumb {background: rgba(0,0,0,0.5)}
.scroll-block::-webkit-scrollbar-thumb {background: rgba(255,255,255,0.1)}
.scroll-block:hover::-webkit-scrollbar-thumb {background: rgba(255,255,255,0.5)}

html { overflow:  hidden; }

.close {
  float: right;
  font-size: 21px;
  font-weight: bold;
  line-height: 1;
  text-shadow: 0 1px 0 #ffffff;
  filter: alpha(opacity=20);
}
.close:hover,
.close:focus {
  background-color: #fff;
  color: #006697;
  text-decoration: none;
  cursor: pointer;
  filter: alpha(opacity=50);
}
button.close {
  color: #fff;
  border-radius: 5px;
  padding: 1px 3px 1px 3px;	
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
}

.form-control {
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #FFF;
  background-color: rgba(0,0,0,0.5);
  background-image: none;
  border: 1px solid #000;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
  -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
}

.form-control-white {
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    -webkit-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    -webkit-transition: border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
}
.form-control:focus,
.form-control-white :focus {
  border-color: #009688 ;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(0, 137, 123, 0.6);;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(0, 137, 123, 0.6);
}

.btn {
    padding: 3px 30px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: normal;
    line-height: 24px;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 0px;
}
.btn:hover,
.btn.focus {
  color: #333333;
  text-decoration: none;
}
.btn:active,
.btn.active {
  outline: 0;
  background-image: none;
  -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}
.btn-default {
  color: #333333;
  background-color: #fff;
  border-color: #cccccc;
}
.btn-default:hover,
.btn-default.focus {
  color: #333333;
  background-color: #e6e6e6;
  border-color: #adadad;
}

.btn-primary {
  color: rgba(255,255,255,0.8);
  background-color: #009688;
  border-color: #00897b ;
}
.btn-primary:hover,
.btn-primary.focus {
  color: #ffffff;
  background-color: #00796b ;
  border-color: #00695c ;
}

.btn-success {
  color: #ffffff;
  background-color: #5cb85c;
  border-color: #4cae4c;
}
.btn-success:hover,
.btn-success.focus {
  color: #ffffff;
  background-color: #449d44;
  border-color: #398439;
}
.btn-info {
  color: #ffffff;
  background-color: #5bc0de;
  border-color: #46b8da;
}
.btn-info:hover,
.btn-info.focus {
  color: #ffffff;
  background-color: #31b0d5;
  border-color: #269abc;
}
.btn-warning {
  color: #ffffff;
  background-color: #f0ad4e;
  border-color: #eea236;
}
.btn-warning:hover,
.btn-warning.focus {
  color: #ffffff;
  background-color: #ec971f;
  border-color: #d58512;
}

.btn-danger {
  color: #ffffff;
  background-color: #d9534f;
  border-color: #d43f3a;
}

.btn-danger:hover,
.btn-danger.focus {
  color: #ffffff;
  background-color: #c9302c;
  border-color: #ac2925;
}
.btn-lg {
  padding: 7px 30px;
  font-size: 18px;
  line-height: 1.33;
}
.btn-sm {
  padding: 5px 10px;
  font-size: 12px;
  line-height: 1.5;
  border-radius: 3px;
}
.btn-xs {
  padding: 1px 5px;
  font-size: 12px;
  line-height: 1.5;
  border-radius: 3px;
}


.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}


