#speedometer 
{
	position: absolute;
	z-index: 10;
	right: 0px;
	bottom: 0px;
}


#speedometer .speed-shadow {
    background-image: url(../img/speedometer/ShadowMain.png);
    background-size: cover;
    position: absolute;
    bottom: -90px;
    width: 300px;
    height: 300px;
	opacity: 0.7;
    right: 20px;
}

#speedometer .speed {
    background-image: url(../img/speedometer/speedometer.png);
    background-size: cover;
    position: relative;
    width: 300px;
    height: 300px;
}

#speedometer .fuel-shadow {
    background-image: url(../img/speedometer/ShadowLeft.png);
    background-size: cover;
    position: absolute;
	bottom: -140px;
    width: 300px;
    height: 300px;
	opacity: 0.7;
    right: 185px;
    -webkit-transform: rotate(1deg);
        -ms-transform: rotate(1deg);
            transform: rotate(1deg);
}

#speedometer .fuel {
    background-image: url(../img/speedometer/fuel.png);
    background-size: cover;
    position: relative;
	width: 300px;
    height: 300px;
    -webkit-transform: rotate(1deg);
        -ms-transform: rotate(1deg);
            transform: rotate(1deg);
}

#speedometer .speed-cursor {
    background-image: url(../img/speedometer/cursorBig.png);
    background-size: cover;
	width: 300px;
    -webkit-transition: 0.3s linear;
    -o-transition: 0.3s linear;
    transition: 0.3s linear;
	position: absolute;
    height: 300px;	
}

#speedometer .fuel-cursor {
    background-image: url(../img/speedometer/cursorSmall.png);
    background-size: cover;
    width: 300px;
    -webkit-transition: 0.3s linear;
    -o-transition: 0.3s linear;
    transition: 0.3s linear;
	position: absolute;
    height: 300px;	
}

#speedometer .fuel-logo {
    background-image: url(../img/speedometer/fuelLogo.png);
    background-size: cover;
	width: 300px;
	position: absolute;
    height: 300px;	
}

#speedometer .lock-logo {
    background-size: cover;
	width: 300px;
	position: absolute;
    height: 300px;	
}

#speedometer .lock-logo.on {
    background-image: url(../img/speedometer/lockOn.png);
    opacity: 0.8;
}

#speedometer .lock-logo.off {
    background-image: url(../img/speedometer/lockOff.png);    
    opacity: 0.8;
}


#speedometer .engine-logo {
    background-size: cover;
	width: 300px;
	position: absolute;
    height: 300px;	
}

#speedometer .engine-logo.on {
    background-image: url(../img/speedometer/engineOn.png);
    }
    
#speedometer .engine-logo.off {
    background-image: url(../img/speedometer/engineOff.png);       
    }




