#police {
	position: absolute;
	width: 910px;
	height: 70%;
	position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
	z-index: 2;	
}

#police .left {
    float: left;
    list-style-type: none;
    background: rgba(13, 71, 161, 0.8);
    margin: 0px;
    padding: 0px;
    color: #fff;
    text-align: center;
    width: 200px;
    height: 100%
}

#police .header {
    background: rgba(0, 0, 102, 0.3);
    font-size: 24px;
    text-align: center;
    padding: 10px 0px;
} 

#police .right {
    float: right;
    list-style-type: none;
    margin: 0px;
    padding: 0px;
    background: rgba(13, 71, 161, 0.8);
    color: #fff;
    width: 700px;
    height: 100%
}

#police .right .alert {
    padding: 5px 15px;
    margin-top: 5px;
    text-align: center;
    background: rgba(0, 0, 102,0.3);
}

#police .right .label {
    font-weight: bold;
    width: 130px;
    padding-top: 7px;
    float: left;
}

#police .right .form-control-white {
    float: left;
}

#police .right .header .glyphicons.glyphicons-remove {
    right: 14px;
    font-size: 20px;
    top: 14px;
    position: absolute;
}
#police .right .header .glyphicons.glyphicons-remove:hover {
    right: 12px;
    font-size: 24px;
    top: 12px;      
}

#police .right .header .glyphicons.glyphicons-refresh {
    right: 40px;
    font-size: 16px;
    top: 16px;
    position: absolute;
}
#police .right .header .glyphicons.glyphicons-refresh:hover {
    right: 38px;
    font-size: 20px;
    top: 14px;       
}

#police .right .header .glyphicons.glyphicons-chevron-left {
    left: 224px;
    font-size: 18px;
    top: 16px;
    position: absolute;
}
#police .right .header .glyphicons.glyphicons-chevron-left:hover {
    left: 222px;
    font-size: 22px;
    top: 14px;       
}

#police .right .send {
    padding: 8px 30px;
    margin: 20px;
    font-size: 18px;
    color: #fff;
    background-color: rgba(0, 0, 102,0.3);
}
#police .right .send:hover {
    padding: 8px 30px;
    margin: 20px;
    font-size: 18px;
    color: #0d47a1;
    background-color: white;
}

#police .right .nameList {
    position: relative;
    float: right;
    width: 300px;
    height: 100%;
}

#police .right .nameList ul {
    height: calc(70vh / 2);
}

#police .right .nameList ul,
#police .left ul {
	color: #fff;
	padding: 0px;
	margin: 0px;
}

#police .right .nameList li,
#police .left li
{
    padding: 10px;
    list-style-type: none;
    font-size: 16px;
	background: rgba(0, 0, 102,0.3);
	margin: 5px;
}

#police .right .nameList li.active,
#police .right .nameList li:hover,
#police .left li.active,
#police .left li:hover
{
    background: white !important;
    color: #0d47a1;
}

#police .right .nameList .glyphicons.glyphicons-refresh {
    top: 0px;
    right: 20px;
    margin: -10px;
    position: absolute;
    padding: 5px 15px;
    background: rgba(0, 0, 102,0.3);
}
#police .right .nameList .glyphicons.glyphicons-refresh:hover {
    color: #0d47a1;
    background-color: white;
}

#police .right .nameList b {
    font-size: 18px;
    padding: 10px;
}

#police .right #wanted,
#police .right #member,
#police .right #info,
#police .right #stats,
#police .right #call { 
    height: calc(100% - 47px - 40px);
}

#police .right #wanted .glyphicons.glyphicons-star-empty,
#police .right #wanted .glyphicons.glyphicons-star {
    font-size: 14px;
    padding: 1px 5px;
}

#police .right #wanted .name,
#police .right #call .name,
#police .right #call .reason,
#police .right #call .distance,
#police .right #call .glyphicons-ok,
#police .right #call .glyphicons-remove,
#police .right #wanted .star,
#police .right #member .name,
#police .right #member .rank,
#police .right #member .glyphicons,
#police .right #wanted .glyphicons-search,
#police .right #wanted .glyphicons-remove,
#police .right #stats .title,
#police .right #stats .stat,
#police .right #info .title,
#police .right #info .stat,
#police .right #info .name,
#police .right #info .star,
#police .right #info .date,
#police .right #info .price,
#police .right #info .reason,
#police .right #info .glyphicons-remove {
    background: rgba(0, 0, 102,0.3);
    float: left;
    margin: 2px;
    padding: 10px;
}

#police .right #info .price {
    width: 70px;
    text-align: center;   
}

#police .right #info .date {
    width: 130px;
    text-align: center;
}

#police .right #info .reason {
    width: 304px;
}

#police .right #info .header-fine {
    font-weight: bold;
    font-size: 24px; 
}

#police .right #info .glyphicons-remove,
#police .right #call .glyphicons-remove,
#police .right #call .glyphicons-ok {
    width: 50px;
    font-size: 18px;
    text-align: center;
}

#police .right #info .glyphicons-remove:hover,
#police .right #call .glyphicons-remove:hover {
    background-color: red;
}

#police .right #call .name,
#police .right #call .reason {
    width: 180px;
}

#police .right #call .distance {
    width: 70px;
}

#police .right #stats .title,
#police .right #info .title {
    width: 297px;  
}

#police .right #info .star span {
 margin-left: 10px;
}

#police .right #stats .stat,
#police .right #info .name,
#police .right #info .star {
    width: 297px;
}

#police .right #member .glyphicons {
    width: 30px;
    text-align: center;
}

#police .right #member .rank {
    width: 150px;
    text-align: center;
}

#police .right #member .glyphicons-plus:hover,
#police .right #call .glyphicons-ok:hover {
    background-color: green;
}

#police .right #member .glyphicons-minus:hover,
#police .right #member .glyphicons-user-remove:hover {
    background-color: red;
}

#police .right #wanted .name {
    width: 294px;
}

#police .right #wanted .star {
    width: 120px;
    text-align: center;
}

#police .right #wanted .glyphicons-search:hover {
    color: #0d47a1;
    background-color: white;
}

#police .right #wanted .glyphicons-remove:hover {
    background-color: red;
}

#police .right #wanted .glyphicons-search,
#police .right #wanted .glyphicons-remove{
    width: 70px;
    text-align: center;
}


#police .right #search,
#police .right #fine,
#police .right #info,
#police .right #wanted,
#police .right #member,
#police .right #stats,
#police .right #call {
    padding: 20px;
}

#police .right #search .glyphicons.glyphicons-star-empty,
#police .right #search .glyphicons.glyphicons-star {
    font-size: 30px;
    padding: 5px;
}


