#shop {
	position: absolute;
	width: 655px;
	height: 70%;
	position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
	z-index: 2;		
}

#shop .shop-header 
{
    height: 5vh;
    width: 609px;
    float: left;
	text-align: center;
	line-height: 5vh;
	font-size: 16px;
	text-transform: uppercase;
	background: #009688;
	color: #fff;
	font-weight: bold;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}

#shop .shop-close
{
    height: 5vh;
    color: #fff;
	width: 30px;
	float: right;
	margin-right: 10px; 
	text-align: center;
	line-height: 5vh;
	font-size: 16px;
	background: #009688;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}


#shop .shop-close:hover {
	background: #00796b;
}



#shop .shop-menu {
    float: left;
    list-style-type: none;
    margin: 0px;
    padding: 0px;
    color: #fff;
    text-align: center;
    width: 100px;
}

#shop .shop-menu li.active {
    padding: 8px;
    background-color:  #009688;
    margin: 5px 0px 5px 0px;
}

#shop .shop-menu li{
    padding: 8px;
    background-color: rgba(0, 0, 0, 0.5);
    margin: 5px 5px 5px 0px;
}

#shop .shop-body {
    margin-top: 5px;
    width: 550px;
    float: right;
    max-height: 60vh;
    border-left: 5px solid  #009688;
}

#shop .shop-body .item {
    float: left;
    height: 85px;
    margin: 0px 0px 5px 5px;
    border: 1px solid #000;
    background-color: rgba(0, 0, 0, 0.5);
}

#shop .shop-body .item.focus  .description {
    background-color: #009688;
}

#shop .shop-body .message {
    background-color:  #009688;
    width: 173px;
    color: #fff;
    font-weight: bold;
    text-shadow: 1px 1px 0px rgb(0, 0, 0);
    height: 60px;
    padding-top:25px;
    text-align: center
}


#shop .shop-body .item.focus .description {
    color: #fff;
    text-shadow: 1px 1px 0px rgb(0, 0, 0);
}

#shop .shop-body .item img
{
    padding: 5px;
    text-align: center;
    background: rgba(0, 0, 0, 0.5);
    border-right: 1px solid #000;
}

#shop .shop-body .item.focus img
{
    padding: 5px;
    text-align: center;
    background: rgba(0, 0, 0, 0.5) ;
    border-right: 1px solid #004d40 ;
}

#shop .shop-body .item .description {
    padding: 5px;
    width: 77px;
    line-height: 30px;
    text-shadow: 1px 1px 0px rgb(0, 0, 0);
    height: 77px;
    color: #fff;
    float: right;
}


