#atm {
	background: -webkit-gradient(linear, right bottom, left top, from(#0277bd) , to(#03a9f4) );
	background: -webkit-linear-gradient(bottom right, #0277bd , #03a9f4 );
	background: -o-linear-gradient(bottom right, #0277bd , #03a9f4 );
	background: linear-gradient(to top left, #0277bd , #03a9f4 );
	width: 1000px;
	-webkit-box-shadow: 0px 0px 2px 2px rgba(0,0,0,0.5);
	        box-shadow: 0px 0px 2px 2px rgba(0,0,0,0.5);
	height: 600px;
	border-radius: 50px;
	z-index: 10;
	position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

#atm .atm_body {
	background-color: #e9edf5;
	margin: 50px auto;
	border-radius: 20px;
	text-align: center;
	height: 450px;
	padding: 25px;
	width: 800px;
	-webkit-box-shadow: inset 0px 0px 5px 5px rgba(0,0,0,0.5);
	        box-shadow: inset 0px 0px 5px 5px rgba(0,0,0,0.5);
}

#atm input {
	padding: 12px;
	width: 300px;
    font-size: 24px;
    line-height: 1.42857143;
    color: #03a9f4;
    background-color: #fff;
    border: 1px solid #81d4fa;	
}

#atm button {
	margin: 30px;
}

#atm .atm_body button {
	vertical-align: middle;
	border: 1px solid transparent;
	color: #fff;
    background-color: #03a9f4;
	border-color: #0277bd;
	margin: 70px;
    padding: 12px 24px;
    font-size: 24px;
    line-height: 1.42857143;
    border-radius: 50px;
}

#atm button.focus {
	background: -webkit-gradient(linear,  right bottom, left top,  from(#0277bd),to(#01579b));
	background: -webkit-linear-gradient(bottom right,  #0277bd,#01579b);
	background: -o-linear-gradient(bottom right,  #0277bd,#01579b);
	background: linear-gradient(to top left,  #0277bd,#01579b);
}

#atm .atm_title {
    text-align: center;
    margin: 50px;
	font-size: 56px;
    color: #03a9f4;
    font-weight: bold;
    font-family: Myriad Pro, Segoe UI, Verdana, sans-serif;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.5);	
}

#atm p {
	color: #03a9f4;
	font-size: 18px;
    font-family: Myriad Pro, Segoe UI, Verdana, sans-serif;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.5);	
}

#atm .atm_item {
	float: left;
	margin: 50px 50px;
    text-align: center;
}

#atm .atm_item p {
	color: #03a9f4;
	font-size: 18px;
    font-family: Myriad Pro, Segoe UI, Verdana, sans-serif;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.5);	
}

#atm .atm_item span {
	background: -webkit-gradient(linear, right bottom, left top, from(#4fc3f7), to(#03a9f4) );
	background: -webkit-linear-gradient(bottom right, #4fc3f7, #03a9f4 );
	background: -o-linear-gradient(bottom right, #4fc3f7, #03a9f4 );
	background: linear-gradient(to top left, #4fc3f7, #03a9f4 );
    padding: 24px;
    color: #fff;
    border-radius: 50px;
    font-size: 48px;
}

#atm .atm_item.focus span {
	background: -webkit-gradient(linear,  right bottom, left top,  from(#0277bd),to(#01579b));
	background: -webkit-linear-gradient(bottom right,  #0277bd,#01579b);
	background: -o-linear-gradient(bottom right,  #0277bd,#01579b);
	background: linear-gradient(to top left,  #0277bd,#01579b);
	 	
}


