#dialog
{
	position: absolute;
	width: 600px;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
	color: #fff;	
	font-family: sans-serif;
	z-index: 2;
}

#dialog .header 
{
	height: 5vh;
	text-align: center;
	line-height: 5vh;
	font-size: 16px;
	text-transform: uppercase;
	background: #009688;
	font-weight: bold;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}

#dialog .body
{
	background: rgba(0, 0, 0, 0.5);
	margin-top: 5px;
	max-height: 60vh;
	padding: 10px;
}

#dialog .body div {
	text-align: center;
}

#dialog .body input {
    width: 400px;
    margin: 8px;
}

#dialog .body label {
	margin-left: 10px;
	font-weight: bold;
    color: #ccc;
}

#dialog .left
{
	margin: 5px 5px 0px 0px;
	width:295px;
    line-height: 30px;
	float:left;
	height: 30px;
	text-align:center;
	background: rgba(0, 0, 0, 0.5);
}

#dialog .right
{
	margin: 5px 0px 0px 5px;
	height: 30px;
	line-height: 30px;
	width:295px;
	float:left;
	text-align:center;
	background: rgba(0, 0, 0, 0.5);
}

#dialog .active
{
	background: rgba(0, 0, 0, 0.8) !important;
}


