#chat
{
	z-index: 0;
	position: absolute;
	color: white;
	font-family: Myriad Pro, Segoe UI, Verdana, sans-serif;
	text-shadow: 1px 1px 0px rgb(0, 0, 0);
	font-size: 14px;
	line-height: 20px;
}

#chat #chat_messages 
{
	overflow: hidden;
	height: 250px;
	width: 35vw;
}

#chat #chat_messages ul
{
	height: 230px;
	padding: 10px 0px 0px 10px;
    display: table-cell;
    vertical-align: bottom;
}

#chat #chat_messages li {
	list-style-type: none;
}

#chat input
{
	color: white;
	background: rgba(0, 0, 0, 0.5);
	outline: none;
	border: none;
	font-family: Myriad Pro, Segoe UI, Verdana, sans-serif;
	font-size: 13px;
	line-height: 35px;
	width: 33vw;
	padding: 5px 1vw;
}


