.auth
{
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;	
	color:#FFFFFF;
	text-align: center;
	z-index: 2;
	top: 20vh;
}
.auth .auth-img {
	text-align:center;
	
}
.auth .auth-header
{
	background: #009688;
	padding: 8px;
	color: #fff;
	font-weight: bold;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}

.auth .auth-body
{
	background: rgba(0, 0, 0, 0.5);
	margin: auto;
	width: 350px;
    -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.5);
            box-shadow: 0 0 10px rgba(0,0,0,0.5);
    padding: 5px;
    border: 1px solid rgba(0,0,0,0.2);
}

.auth .auth-form {
	padding: 10px 20px;
}

.auth .auth-form button {
	margin: 2px;
}

#personage 
{
	position: absolute;
	width: 370px;
	overflow-x: hidden;
    max-height: 90vh;
	text-align:center;
	margin-top: 5vh;
	right: 5vw;
	font-family: sans-serif;
	z-index: 2;	
}
#personage #features-body .row,
#personage #appearance-body .row  {
    border-bottom: 1px solid rgba(255,255,255,0.2);
    border-top: 1px solid rgba(0,0,0,0.4);
	
}
#personage .header 
{
	text-align: center;
	height: 50px;
	line-height: 50px;
	font-size: 18px;
	text-transform: uppercase;
	background: #009688;
	color: #fff;
	font-weight: bold;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}

#personage .item {
	font-size: 16px;
	padding: 7px 0px;
	margin: 5px 0px;
	color: #fff;
	font-weight: bold;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	background: rgba(0,0,0,0.3);
}
#personage .range label {
	position: relative;
    bottom: 9px;
}

#personage #heredity-body .focus.block {
	color:#383838;
    background-color: rgba(255, 255, 255, 0.5) !important;
	-webkit-box-shadow: 0px 0px 5px rgba(0,0,0,0.8) !important;
	        box-shadow: 0px 0px 5px rgba(0,0,0,0.8) !important;	
}

#personage  .section
 {
	max-height: 50vh;
	outline: 1px solid black !important;
    background-color: rgba(0, 0, 0, 0.8) !important;
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.4) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.4) inset;	
}


#personage .glyphicons:hover {
	color:#A0A0A0;
}

#personage .glyphicons:hover {
	color:#606060 ;
}


#personage .item .left {
	float: left;
	padding-left:10px;
}

#personage .block {
    float: left;
    width: 176px;
    margin: 10px 0px 10px 0px;	
}

#personage .item .right {
	text-align:right;
}

#personage img {
	width:75px;
	margin-top:10px;
	margin-bottom:10px;
}

#personage #heredity-body .glyphicons {
	vertical-align: top!important;
	margin: 40px 3px 3px 0px;
	font-size: 24px;
}

#personage .range {
	background: none!important;
	outline: none!important;
	color:#fff!important;
	-webkit-box-shadow: none!important;
	        box-shadow: none!important;	
}

#personage .range.focus input[type=range]::-webkit-slider-runnable-track,
#personage .focus input[type=range]::-webkit-slider-runnable-track 
{
 background: #26a69a;
}

#personage .range.focus input[type=range]::-webkit-slider-thumb,
#personage .focus input[type=range]::-webkit-slider-thumb {
	background: #fff;
}

#personage #heredity-body input[type=range] {
	width: 250px;
}
#personage #features-body input[type=range] {
	width: 150px;
}

#personage #features-body input[type=range].mini {
	width: 130px;
}

#personage #appearance-body input[type=range] {
	width: 155px;
}

#personage #clothing-body input[type=range] {
	width: 200px;
}

input[type=range]::-webkit-slider-runnable-track {
 width: 100%;
 height: 8.4px;
 cursor: pointer;
 -webkit-box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
         box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
 background: #26a69a;
 border-radius: 1.3px;
 border: 0.2px solid #010101;
}

input[type=range] {
	height: 26px;
	padding-right: 10px;
	-webkit-appearance: none;
	margin: 0px;
	background: none;
	outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 14px;
  -webkit-box-shadow: 1px 1px 1px #50555C;
          box-shadow: 1px 1px 1px #50555C;
  background: #50555C;
  border-radius: 14px;
  border: 0px solid #000000;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-box-shadow: 0px 0px 0px #000000;
          box-shadow: 0px 0px 0px #000000;
  border: 0px solid #000000;
  height: 20px;
  width: 36px;
  border-radius: 12px;
  background: #26a69a;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -3px;
}

.input-auth {
	position: relative;
	margin-top: 16px;
}

.input-auth input {
	background-color: transparent;
	color: #fff;
    border: none;
    border-bottom: 1px solid #9e9e9e;
    border-radius: 0;
    outline: none;
    height: 28px;
    width: calc(100% - 54px);
    font-size: 16px;
    margin: 10px 20px 10px 36px;
    padding: 0px 0px 5px 0px;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.input-auth.focus input {
	border-bottom: 1px solid #26a69a;
}

.input-auth.focus label {
	color: #26a69a;
}

.input-auth.focus .glyphicons  {
	color: #26a69a;
}

.input-auth.select input {
	border-bottom: 1px solid #fff;
}

.input-auth.select label {
	color: #fff;
}

.input-auth.select .glyphicons  {
	color: #fff;
}

.input-auth .glyphicons  {
	position: absolute;
	bottom: 17px;
	color: #9e9e9e;
	font-size: 20px;
}


.input-auth label {
	color: #9e9e9e;
    position: absolute;
    top: 0;
    left: 0;
	height: 100%;
	margin-left: 36px;
    font-size: 16px;
    cursor: text;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
    -webkit-transform-origin: 0% 100%;
        -ms-transform-origin: 0% 100%;
            transform-origin: 0% 100%;
    text-align: initial;
    -webkit-transform: translateY(12px);
        -ms-transform: translateY(12px);
            transform: translateY(12px);
    pointer-events: none;
}

.input-auth.focus label:not(.label-icon),
.input-auth.select label:not(.label-icon)
 {
	-webkit-transform: translateY(-14px) scale(0.8);
		  -ms-transform: translateY(-14px) scale(0.8);
	    transform: translateY(-14px) scale(0.8);
	-webkit-transform-origin: 0 0;
		  -ms-transform-origin: 0 0;
	    transform-origin: 0 0;
}


