#binder {
	color: #fff;
	position: absolute;
	width:600px;
	z-index: 2;
	height: 70%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

#binder .bind-header
{
	margin: auto;
	height: 5vh;
	width:554px;
	float: left;
	text-align: center;
	line-height: 5vh;
	font-size: 16px;
	text-transform: uppercase;
	background: #009688;
	font-weight: bold;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}

#binder tbody  {
	margin-left: -4px;
	
}

#binder tbody .create th,
#binder tbody .update th
{
	padding: 0px;
	
}

#binder .bind-close
{
	height: 5vh;
	width: 30px;
	float: right;
	margin-right: 10px; 
	text-align: center;
	line-height: 5vh;
	font-size: 16px;
	background: #009688;
	border: 1px solid rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
	        box-shadow: 0 1px 0 rgba(255,255,255,0.3) inset;
}

#binder .bind-close:hover {
	background: #00796b;
}

#binder .bind-list {
	max-height:60vh;
	position: absolute;
}

#binder .bind-key {
	width: 100px;
	text-align: center;
	background-color: rgba(0, 0, 0, 0.5);
	margin-right: 3px;
}

#binder .bind-name {
	width: 300px;
	max-height: 200px;
	background-color: rgba(0, 0, 0, 0.5);
	margin-right: 3px;
}

#binder .bind-auto {
	margin-right: 3px;
	background-color: rgba(0, 0, 0, 0.5);
	width: 50px;	
}

#binder .bind-block {
	width: 69px;
}

#binder .bind-block span {
	padding: 8px;
}

#binder .bind-block .glyphicons-remove {
	margin-left: 3px; 
	background-color: #bf360c;
}

#binder .bind-block .glyphicons-cogwheel {
	background-color: #00796b;
}

#binder .bind-block .glyphicons-cogwheel:hover {
	background-color: #26a69a;
}

#binder .bind-list .glyphicons-edit {
	background-color: #00796b;
	width: 52px;
	padding: 8px;
	margin: 1px 3px 1px 3px;
	text-align: center;
}

#binder .update .bind-auto .glyphicons {
	background:none!important;
}

#binder .bind-list .glyphicons-edit:hover {
	background-color: #26a69a;
}

#binder .bind .bind-block .glyphicons-remove:hover {
	background-color: #ff5722;
}

#binder .bind-list input::-webkit-input-placeholder { color: #e0e0e0; }
#binder .bind-list input {
	padding: 5px 12px;
}

#binder .bind-list #keyUp {
	text-align: center;
	width: 76px;
	margin: 0px;
	float: left;
}

#binder .bind-list #messageBind {
	margin: 0px;
	width: 333px;
	float: left;
}

#binder .bind-list #keyUpUpdate {
	text-align: center;
	width: 76px;
	margin: 0px;
	float: left;
}

#binder .bind-list #messageBindUpdate {
	margin: 0px;
	width: 333px;
	float: left;
}


#binder .bind-list .glyphicons-ok-circle,
#binder .bind-list .glyphicons-ban-circle {
	width: 36px;
	margin-right: -1px;
	margin-left: -1px;
	text-align: center;
	padding: 8px;
	float: left;
}

#binder .bind-list .create .glyphicons-ok-circle,
#binder .bind-list .update .glyphicons-ok-circle {
	background-color: #388e3c;
	margin-left: 0px;
}

#binder .bind-list .create .glyphicons-ban-circle,
#binder .bind-list .update .glyphicons-ban-circle  {
	background-color: #bf360c;
	margin-left: 0px;
}

#binder .bind-list .create .glyphicons-ok-circle:hover,
#binder .bind-list .update .glyphicons-ok-circle:hover  {
	background-color: #4caf50;
}

#binder .bind-list .create .glyphicons-ban-circle:hover,
#binder .bind-list .update .glyphicons-ban-circle:hover  {
	background-color: #ff5722;
}




